@import (less) "color.less";
.menu {
    position: relative;
    width: 100%;
    height: 0;
    overflow-x: hidden;
    overflow-y: hidden;
    background-color: @darkest_gray;
    -moz-transition: height, opacity 0.7s ease-out;
    -o-transition: height, opacity 0.7s ease-out;
    transition: height, opacity 0.7s ease-out;
    .icon {
        margin-right: 0.313em;
        font-size: 1.563em;
        vertical-align: middle;
    }
}

.menu.closed {
    height: 0;
}

.menu.open {
    // height: 23em;
}

.main-menu-container {
    padding: 0;
}

.section-menu:nth-child(2n+3) {
    clear: left;
}

.main-menu {
    list-style: none;
    padding: 0 0.313em 0 0.313em;
    margin: 0;
    a {
        color: @light_gray;
        display: table;
        width: 19.375em;
        padding: 0em 1.250em 0em 1.250em;
        margin: .5em 0 .5em 0;
        text-decoration: none;
        cursor: pointer;
        height: 2.813em;
        &:hover, &:focus, &:active {
            background-color: @green;
            color: @white;
        }
        span {
            display: table-cell;
            vertical-align: middle;
            text-align: left;
            &:nth-child(1) {
                width: 1.563em;
            }
        }
    }
    .select {
        background-color: @green;
        color: @white;
        border: 0.0625em solid @green;
    }
}

.upper-bar {
    border-bottom: 0.0625em solid @gray;
}

.menu-principale {
    color: @light_gray;
    font-size: 1em;
    padding: 1em 0.625em 0.625em 0.625em;
    cursor: pointer;
    .icon {
        font-size: 0.875em;
    }
}

.menu-principale-title {
    vertical-align: top;
    -moz-animation: fadein 3.7s ease-out;
    animation: fadein 3.7s ease-out;
}

@-moz-keyframes fadein {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadein {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.selected-first-level-menu {
    color: @light_gray;
    padding: 1.625em .625em 1.62em .625em;
    .selected-first-level-label {
        font-size: 1.125em;
    }
}

.first-level-menu {
    position: absolute;
    left: 0;
    visibility: hidden;
    right: 0;
    opacity: 0;
    left: -6.250em;
    padding: 0 0.625em 0 0.625em;
    -moz-transition: all 0.7s ease-out;
    -o-transition: all 0.7s ease-out;
    transition: all 0.7s ease-out;
}

.first-level-menu.open {
    opacity: 1;
    left: 0;
    visibility: visible;
    margin-top: 0.625em;
}

.second-level-menu {
    position: absolute;
    left: 0;
    visibility: hidden;
    right: 0;
    opacity: 0;
    left: 6.250em;
    -moz-transition: all 0.7s ease-out;
    -o-transition: all 0.7s ease-out;
    transition: all 0.7s ease-out;
}

.second-level-menu.open {
    opacity: 1;
    left: 0;
    visibility: visible
}

//
@media (min-width: 768px) {
    overflow-y: auto;
}

@media (max-width: 992px) {
    .main-menu {
        a {
            width: 14.375em;
        }
    }
}

:host-context(.contrasto-on) {
    .menu {
        background-color: @black;
        &.open {
            border-color: @white;
        }
    }
    .main-menu {
        a {
            color: @white;
            border-color: @white;
            &:hover, &:focus, &:active {
                background-color: @white;
                color: @black;
                border-color: @white;
            }
        }
        .select {
            background-color: @white;
            color: @black;
            border-color: @white;
        }
    }
    .upper-bar {
        border-color: @white;
    }
    .menu-principale {
        color: @white;
    }
    .selected-first-level-menu {
        color: @white;
    }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    [class*=' icon-']::before {
        font-size: 1.375em !important;
    }
}